import Vue from 'vue';
import Vuex from 'vuex';
import axios from '@/axios'

Vue.use(Vuex);

export default new Vuex.Store({

    state: {
        currentUser: [],
        movies: [],
        schedules: [],
        statusLogin: false
    },
    getters: {
        getCurrentUser(state){
            return state.currentUser
        },
        getMovies(state){
            return state.movies
        },
        getSchedules(state){
            return state.schedules
        },
        getStatusLogin(state){
            return state.statusLogin
        }
    },
    actions: {
        showErrors(context, err){
            console.log(err.config);
            if (err.response) {
                console.log(err.response.data);
                console.log(err.response.status);
                console.log(err.response.headers);
            } else if (err.request) {
                console.log(err.request);
            } else {
                console.log('Error', err.message);
            }
        },
        allMovies(context){
            axios.get('getMovie')
                .then((res)=>{
                    context.commit('movies', res.data)
                }, 200)
                .catch((err)=>{
                    this.dispatch('showErrors', err)
                })
        },
        currentUser(context){
            context.commit('currentUser', localStorage.user)
        },
        scheduleMovie(context, id){
            axios.get('getDataMovie/' + id)
            .then((res)=>{
                context.commit('schedules', res.data)
            }, 200)
            .catch((err)=>{
                this.dispatch('showErrors', err)
            })
        },
        isLogued(context){
            if (localStorage.access_token) {
                context.commit('statusLogin', true)
            } else {
                delete localStorage.user
                this.dispatch('currentUser', null)
                context.commit('statusLogin', false)
            }
        }
    },
    mutations: {
        currentUser(state, data){
            return state.currentUser = data
        },
        movies(state, data){
            return state.movies = data
        },
        schedules(state, data){
            return state.schedules = data
        },
        statusLogin(state, data){
            return state.statusLogin = data
        }
    }
});